import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../dependencies/empresaMLFDependency';
import { CLASS_NAME, TipoMovimentoInterno } from '../../../../models/dtos/mlf/tipoMovimentoInterno';
import { TIPO_MOVIMENTO_INTERNO_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoMovimentoEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'tipoStr',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const TipoMovimentacaoInternaSearchView: FC<SearchViewProps<TipoMovimentoInterno>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const grupoTipoMovimentacaoInterna = globalReducer.globalParameter.grupoEmpresaConfig.grupoTipoMovimentacaoInterna;

  const columns: ColumnsProps[] = [
    enumColumn(
      {
        dataField: 'tipoStr',
        editable: false,
        sort: true,
        text: 'Tipo',
      },
      TIPO_MOVIMENTO_INTERNO_ENUM_VALUES
    ),
    {
      dataField: 'descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'grupoTipoMovimentacaoInterna.descricao',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Grupo',
    },
  ];

  function getSearchExpression(): SearchExpression[] {
    return [
      {
        fieldName: 'grupoTipoMovimentacaoInterna.id',
        value: grupoTipoMovimentacaoInterna.id,
      },
    ];
  }

  return (
    <WindowComponent
      title="Tipo Movimentação Interna de Lançamento"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
      wikiId={2467}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'TipoMovimentacaoInternaEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default TipoMovimentacaoInternaSearchView;
